// basc
:root {
  --color-primary: #102522;
  --color-secondary: #151442;
  --color-white: #fff;
  --color-light-gray: #8f9095;
  --color-light-gray-1: #cfcfcf;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
}

%centeritem {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin button-produts-link {
  border: 1px solid var(--color-primary);
  color: var(--color-secondary);
  font-size: 1.4rem;
  @extend %font-normal;
  background-image: url(../img/arrow.svg);
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 2rem;
  padding: 1.5rem 3rem;
  min-width: 18.8rem;
  text-align: start;
  background-position-x: 13rem;
  @media(max-width: 48em) {
    min-width: max-content;
    
    font-size: 1.7rem;
    padding: 1rem 5rem;
    padding-left: 2rem;
    background-position-x: 12rem;
  }

}

%text-stroke {
  text-shadow: 1px 1px 0 var(--color-white), -1px -1px 0 var(--color-white),
    1px -1px 0 var(--color-white), -1px 1px 0 var(--color-white),
    1px 1px 0 var(--color-white);
}
//FONT

@font-face {
  font-family: "font-bold";
  src: url("../font/font-bold-webfont.woff2") format("woff2"),
    url("../font/font-bold-webfont.woff") format("woff");
}

@font-face {
  font-family: "font-medium";
  src: url("../font/font-medium-webfont.woff2") format("woff2"),
    url("../font/font-medium-webfont.woff") format("woff");
}

@font-face {
  font-family: "font-normal";
  src: url("../font/font-normal.woff2") format("woff2"),
    url("../font/font-normal.woff") format("woff");
}

%font-normal {
  font-family: "font-normal";
}

%font-medium {
  font-family: "font-medium";
}

%font-bold {
  font-family: "font-bold";
}

html {
  font-size: 62.5%;

  @media(max-width: 75em) {
    font-size: 56.25%;
  }

  @media(max-width: 50em) {
    font-size: 50%;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

body {
  @extend %font-medium;
  font-size: 1.4rem;
  line-height: 1.5;
  color: #313131;
}

.container {
  max-width: 170rem;
  @media(min-width: 120em) {
    max-width: 170rem;
  }
  @media(max-width: 100em) {
    max-width: 170rem;
    padding: 0 10rem;
  }

  @media(max-width: 62.375em) {
    max-width: 100%;
    padding: 0 5rem;
  }
  @media(max-width: 48em) {
    max-width: 100%;
    padding: 0 2rem;
  }
}

section {
  padding: 16rem 0;

  @media(max-width: 87.5em) {
    padding: 12rem 0;
  }

  @media(max-width: 75em) {
    padding: 8rem 0;
  }

  @media(max-width: 62em) {
    padding: 6rem 0;
  }

  @media(max-width: 48em) {
    padding: 4rem 0;
  }
}

// button

.btn {
  border-radius: 3rem;
  padding: 2rem 3rem;
  @media(max-width: 62em) {
    padding: 1.5rem 2rem;
  }

  @media(max-width: 48em) {
    padding: 1.5rem 2rem;
  }

  &:hover {
    transform: translateY(-3px);
    transition: all 0.3s ease-in;
    box-shadow: 0 4px 8px rgba(2, 2, 2, 0.308);
  }

  &:active {
    transform: translateY(0);
    outline: none;
  }

  &:focus {
    box-shadow: none;
  }

  &--green {
    background-color: #c7e1bf;
    color: var(--color-primary);
  }

  &--white {
    background-color: var(--color-white);
    color: var(--color-primary);
  }
}

a,
a:hover,
a:link {
  text-decoration: none;
  color: inherit;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    height: 9.3rem;
    margin-inline-end: auto;
    display: flex;
    align-items: center;
  }

  &__user-control {
    display: flex;
    align-items: center;
    img {
      height: 2.4rem;
    }

    .user-name {
      font-size: 1.6rem;
      margin-inline-start: 5px;
      min-width: 6rem;
    }

    &__item {
      width: 1.9rem;
      height: 1.9rem;
      border-radius: 50%;
      background-color: var(--color-secondary);
      color: var(--color-white);
      margin-inline-end: 1rem;
      padding: 10px 5px;
      line-height: 0;
      flex-shrink: 0;
      @media(max-width: 62em) {
        color:  var(--color-secondary);
        background-color: var(--color-white);
        width: 3.9rem;
        height: 3.9rem;
        padding: 15px 10px;
        font-size: 2rem;
        
       }
    }
  }
}

.navigation {
  margin-inline-end: 8.5rem;
  @media(max-width: 100em) {
    margin-inline-end: 1rem;
  }
  &__list {
    list-style: none;
    padding: 0;
    @extend %font-medium;
    margin-bottom: 0;
    color: var(--color-secondary);
    @media(max-width: 62em) {
     color: var(--color-white);
    }
    @media(max-width: 48em) {
      display: none;
     }
  }

  &__item {
    font-size: 1.4rem;
    @extend %font-medium;
    display: inline-block;
    &:not(:last-child) {
      margin-inline-end: 3.6rem;
    }
  }
}

.hero {
  background-image: linear-gradient(
    to right,
    var(--color-primary) 60%,
    var(--color-white) 60% 100%
  );

  @media(max-width: 85.7em) {
    background-image: linear-gradient(
    to right,
    var(--color-primary) 50%,
    var(--color-white) 50% 100%
  );
  }

  @media(max-width: 62em) {
    background-image: linear-gradient(
      to right,
      var(--color-primary) 100%,
      var(--color-white) 0 0%
    );
  }

  min-height: 100vh;
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: flex-start;
    height: 100vh;
    align-items: flex-start;
    @media(max-width: 48em) {
      flex-direction: column;
      flex-wrap: nowrap;
      padding-top: 6rem;
      align-items: center;
    }

  }
  &__start {
    flex: 0 1 50%;
    @media(max-width: 62em) {
      
    }
    @media(max-width: 48em) {
      flex: 0 1 auto;
      margin-top: auto;
      margin-bottom: 5vh;
      align-self: flex-start;
    }

    &__content {
      margin-inline-start: 12.8rem;
      @media(max-width: 62em) {
        margin-inline-start: 0;
      }
    }

    span {
      font-size: 1.4rem;
      margin-bottom: 1rem;
      letter-spacing: 10px;

      @extend %font-medium;
      color: #d2d2d2;
    }
    p {
      color: #d2d2d2;
      font-size: 2.2rem;
      @extend %font-normal;
      margin-top: 4.6rem;
    }
  }
  h2 {
    font-size: 8.8rem;
    @extend %font-bold;
    color: var(--color-white);
    max-width: 49.8rem;
    @media(max-width: 87.5em) {
      font-size:4.2rem;
    }
    @media(max-width:75em) {
      font-size: 3rem;
    }
    @media(max-width: 62em) {
      max-width: 100%;
      font-size: 3rem;
    }

    @media(max-width: 48em) {
      
      font-size: 3rem;
    }
  }

  &__end {
    flex: 0 1 50%;
    @media(max-width: 62em) {
    }
    @media(max-width: 48em) {
     margin-top: 2vh;
     margin-bottom: 1vh;
     height: 30vh;
     order: -1;
     margin-top: 10rem;
    }
    img {
      height: 70vh;
      max-width: 100%;
      object-fit: contain;
      @media(max-width: 62em) {
        max-width: 100%;
        height: 50vh;
      }
      @media(max-width: 48em) {
       width: 100%;
       height: 40vh;
       }
    }
  }

  .button-group {
    display: flex;
    margin-top: 9.8rem;
    @media(max-width: 48em) {
      margin-top: 3rem;
    }
    a {
      display: inline-block;
      &:first-child {
        margin-right: 5.2rem;
        @media(max-width: 48em) {
          margin-right: 2rem;
        }
      }
    }
  }
}

.products {
  &__head {
    display: flex;
    justify-content: center;
    align-items: center;

    h2 {
      color: var(--color-secondary);
      font-size: 8rem;
      @extend %font-medium;
      margin: 0 auto;
      @media(max-width: 87.5em) {
        font-size:4.2rem;
      }
      @media(max-width:75em) {
        font-size: 3rem;
      }
      @media(max-width: 62em) {
        max-width: 100%;
        font-size: 3rem;
      }
  
      @media(max-width: 48em) {
        
        font-size: 3rem;
      }
    }

    .btn {
      @include button-produts-link;
    }
  }

  &__item-wrap {
    margin-top: 10rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: auto auto;
    column-gap: 8.4rem;
    @media(max-width: 62em) {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 2rem;
      row-gap: 3rem;
    }

    @media(max-width: 48em) {
      grid-template-columns:repeat(2, 1fr);
      row-gap: 1.5rem;
      column-gap: 1.5rem;
    }
  }
}

.product-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  &__title {
    @extend %font-medium;
    font-size: 3.6rem;
    color: var(--color-secondary);
    margin-top: 3.4rem;
    margin-bottom: 1.9rem;
    @media(max-width: 48em) {
      font-size: 2rem;
    }
  }

  &__price {
    @extend %font-normal;
    color: var(--color-light-gray);
    margin-bottom: 2.6rem;
    display: block;
    @media(max-width: 48em) {
      margin-bottom: 2rem;
    }
  }

  &__details {
    color: var(--color-light-gray);
    font-size: 1.6rem;
  }
}
.section-testimonial {
  background-image: url(../img/testmonial_img@2x.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  h2 {
    font-size: 8rem;
    color: var(--color-white);
    @extend %font-medium;
    @media(max-width: 87.5em) {
      font-size: 6.8rem;
    }
    @media(max-width:75em) {
      font-size: 5.8rem;
    }
    @media(max-width: 62em) {
      max-width: 100%;
      font-size: 4.8rem;
    }

    @media(max-width: 48em) {
      font-size: 3rem;
    }
  }
  &__wrap {
    display: flex;
    align-items: center;
    @media(max-width: 62em) {
     flex-direction: column;
    }

   
    & > * {
      flex: 0 1 50%;
      @media(max-width: 62em) {
        flex: 0 1 100%;
        margin: 2rem 0;
      }
    }
  }
}

.section-testimonial-item {
  display: flex;
  flex-direction: column;
  background-color: var(--color-white);
  padding: 8.9rem 10.6rem;
  position: relative;
  @media(max-width: 87.5em) {
    padding: 7rem 8.6rem;
  }
  @media(max-width:75em) {
    padding: 6.9rem 7.6rem;
  }
  @media(max-width: 62em) {
   
    padding: 5.9rem 6.6rem;
  }

  @media(max-width: 48em) {
   
    padding: 3.9rem 3.6rem;
  }

  blockquote {
    font-size: 5rem;
    &::before {
      position: absolute;
      content: "";
      display: inline-block;
      width: 4rem;
      height: 4rem;
      background-image: url(../img/test-colTop.svg);
      background-size: contain;
      background-repeat: no-repeat;
      top: 6rem;
      left: 6rem;
      @media(max-width:75em) {
        width: 3rem;
      height: 3rem;
      top: 4rem;
      left: 3rem;
      }
      @media(max-width: 48em) {
        width: 2rem;
        height: 2rem;
        top: 2rem;
      left: 2rem;
      }
    }
  
    &::after {
      position: absolute;
      content: "";
      display: inline-block;
      width: 4rem;
      height: 4rem;
      background-image: url(../img/test-colBottom.svg);
      background-size: contain;
      background-repeat: no-repeat;
      bottom: 15rem;
      right: 4rem;
      @media(max-width:75em) {
        width: 3rem;
      height: 3rem;
      bottom: 14rem;
      right: 4rem;
      }
      @media(max-width: 48em) {
        width: 2rem;
        height: 2rem;
        bottom: 12rem;
      right: 2rem;
      }
    }
    @extend %font-medium;
    @media(max-width: 87.5em) {
      font-size: 4rem;
    }
    @media(max-width:75em) {
      font-size: 3rem;
    }
    @media(max-width: 48em) {
      font-size: 2.2rem;
    }
  }

  &__user-info {
    margin-left: auto;
    margin-top: 3rem;
    & > * {
      display: block;
    }
    span:first-child {
      font-size: 2.2rem;
      @extend %font-medium;
    }
    span {
      font-size: 1.6rem;
      @extend %font-normal;
    }
  }
}

.prdouct-catagary {
  &__head {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;

    h2 {
      color: var(--color-secondary);
      font-size: 8rem;
      @extend %font-medium;
      margin: 0 auto;
      @media(max-width: 87.5em) {
        font-size: 6.8rem;
      }
      @media(max-width:75em) {
        font-size: 5.8rem;
      }
      @media(max-width: 62em) {
        max-width: 100%;
        font-size: 4.8rem;
      }
  
      @media(max-width: 48em) {
        
        font-size: 3rem;
      }
    }

    .btn {
      @include button-produts-link;
    }
  }

  .slider-bar {
    width: 90%;
    height: 2px;
    margin: 0 auto;
    background-color: var(--color-light-gray);
    margin-top: 4rem;
  }
}

.catagary-slider {
  //slick style
  width: 100%;
  margin: 0 auto;

  .slick-arrow {
    z-index: 2;
  }
  .slick-prev {
    left: -25px;

    &:before {
      content: "";
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      background: url("../img/pre.svg") no-repeat center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      right: 0;
      margin: auto;
      z-index: 2;
      box-shadow: 0 5px 10px rgba(2, 2, 2, 0.308);
    }
  }

  .slick-next {
    right: 5rem;
    &:before {
      content: "";
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      background: url("../img/next.svg") no-repeat center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      right: 0;
      margin: auto;
      z-index: 2;
      box-shadow: 0 5px 10px rgba(2, 2, 2, 0.308);
    }
  }
}

.Our-Benefits {
  padding-top: 0;
  text-align: center;
  h2 {
    color: var(--color-secondary);
    font-size: 8rem;
    @extend %font-medium;
    @media(max-width: 87.5em) {
      font-size: 6.8rem;
    }
    @media(max-width:75em) {
      font-size: 5.8rem;
    }
    @media(max-width: 62em) {
      max-width: 100%;
      font-size: 4.8rem;
    }

    @media(max-width: 48em) {
      
      font-size: 3rem;
    }
  }

  &__wrap {
    display: grid;
    grid-template-columns: repeat(3, minmax(19rem, 23.7rem));
    align-content: center;
    justify-content: center;
    gap: 17.4rem;
    margin-top: 10rem;
    @media(max-width: 48em) {
      grid-template-columns:1fr;
      row-gap: 3rem;
    }
  }
  &__item {
    img {
      margin-bottom: 6.8rem;
      height: 6.4rem;
    }
    p {
      font-size: 1.8rem;
      @extend %font-medium;
      color: var(--color-secondary);
    }
  }
}

.backtop {
  text-align: center;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  cursor: pointer;
  img {
    height: 5rem;
  }
  span {
    display: block;
    color: var(--color-primary);
  }
}
footer {
  background-color: var(--color-primary);
  padding: 8rem 0;
  color: var(--color-white);
  .footer-wrap {
    display: grid;
    grid-template-columns: 1fr repeat(2, 30rem);
    @media(max-width: 48em) {
      grid-template-columns:1fr;
      row-gap: 3rem;
    }

    &__start {
      span {
        @extend %font-normal;
        margin-top: 5.9rem;
        display: block;
      }
    }
  }

  h3 {
    max-width: 41.4rem;

    font-size: 8rem;
    @extend %font-medium;
    @media(max-width: 87.5em) {
      font-size: 6.8rem;
    }
    @media(max-width:75em) {
      font-size: 5.8rem;
    }
    @media(max-width: 62em) {
      max-width: 100%;
      font-size: 4.8rem;
    }

    @media(max-width: 48em) {
      max-width: 100%;
      font-size: 3rem;
    }
  }

  ul {
    list-style: none;
    span {
      margin-bottom: 2.6rem;
      display: block;
      font-size: 1.8rem;
    }
    li {
      line-height: 3;
      @extend %font-normal;
    }
  }

  .address {
    span {
      margin-bottom: 2.6rem;
      display: block;
      font-size: 1.8rem;
    }
  }

  address {
    @extend %font-normal;
    line-height: 2;
  }
}
